import React, { useEffect, useRef, useState } from "react";
import {
  Alert,
  AlertTitle,
  Button,
  Container,
  List,
  ListItem,
  Stack,
} from "@mui/material";
import { AccessTimeFilledRounded } from "@mui/icons-material";
import Swal from "sweetalert2";
import Limit from "./Limit";
import Now from "./Now";
import Today from "./Today";

// Swal.fire("주의사항", "당일 퇴근 할 경우에만 쓰세요", "info");

function Clock() {
  let now = new Date();
  // const [hour, setHour] = useState(now.getHours());
  // const [min, setMin] = useState(now.getMinutes());
  const [sec, setSec] = useState(now.getSeconds());
  const interval = useRef(null);
  const [limitHour, setLimitHour] = useState("18");
  const [limitMin, setLimitMin] = useState("00");

  useEffect(() => {
    interval.current = setInterval(() => {
      now = new Date();
      // setHour(now.getHours());
      // setMin(now.getMinutes());
      setSec(now.getSeconds());
    }, 1000);
    return () => clearInterval(interval.current);
  }, []);

  const setTimerOnClick = (e) => {
    e.preventDefault();
    setLimitHour(document.getElementById("setTimer").value.slice(0, 2));
    setLimitMin(document.getElementById("setTimer").value.slice(3, 5));
  };

  return (
    <Container maxWidth="sm">
      <Stack spacing={2}>
        <Alert severity="error">
          <AlertTitle>
            <strong>주의사항</strong>
          </AlertTitle>
          <strong>당일</strong> 퇴근 할 경우에만 쓰세요
        </Alert>
        <List sx={{ width: "100%", maxWidth: 360, bgcolor: "white" }}>
          <ListItem>
            <Today date={now} />
          </ListItem>
          <ListItem>
            <Now date={now} />
          </ListItem>
          <ListItem>
            <Limit limitHour={limitHour} limitMin={limitMin} />
          </ListItem>
        </List>
        <Stack direction="row" spacing={2}>
          시간 입력 &nbsp;
          <input id={"setTimer"} type={"time"} defaultValue={"18:00"} />
          <Button
            variant="contained"
            onClick={setTimerOnClick}
            startIcon={<AccessTimeFilledRounded />}
          >
            submit
          </Button>
        </Stack>
        <br />
      </Stack>
    </Container>
  );
}
export default Clock;
